*, *:before, *:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
}

svg {
  width: inherit;
}

.i {
  filter: drop-shadow(1.5px 1.5px 1.5px #000);
}
.ipixel {
  filter: drop-shadow(1.5px 1.5px 1.5px rgba(0, 0, 0, 0.5));
}

html {
  font-size: 10px;
}

body {
  color: #3c262b;
  font-size: 3rem;
  font-family: "VT323", Arial, Helvetica, sans-serif;
}

p {
  margin: 2rem 0;
}

a {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-family: "Londrina Solid", Arial, Helvetica, sans-serif;
  font-size: 8rem;
  color: #e7b012;
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  letter-spacing: 1rem;
  text-align: center;
  text-transform: uppercase;
  margin: 2rem 0;
}

main a {
  color: #853605;
}
main a:hover {
  color: #cf5408;
}

footer {
  color: #dde0ee;
  text-align: center;
}
footer a {
  color: #fdbd6f;
}
footer a:hover {
  color: #fc9a24;
}

.stack {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

main {
  background-color: #0261bb;
  background: linear-gradient(#023b7e, #0261bb, #259bfd, #5edceb, #93fedb);
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  padding: 5% 8%;
}

.main-child {
  margin: 3rem 0;
}

.text {
  background-color: #fff;
  background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.1));
  border-radius: 25px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  padding: 1rem 4rem;
}

.i-bus {
  height: 2rem;
}
.i-junimo {
  width: 4rem;
  margin: 1rem 2rem;
}

.credits {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.credits-text {
  margin: 0 0 0 2rem;
}

.i-inventory {
  height: max-content;
}

.acknow-ul {
  padding-left: 0;
}
.acknow-ul-nested {
  margin-left: 4rem;
}
.acknow-li {
  list-style: none;
  margin: 1.5rem 0;
}
.acknow-li-nested {
  list-style: none;
  background-image: url("assets/png/misc/blackberry.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
  background-size: 2.2rem;
  padding-top: 0.5rem;
}

.li-nativeland {
  background-image: url("assets/png/misc/bundle_green.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}
.li-wapikoni {
  background-image: url("assets/png/misc/bundle_yellow.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}
.li-support > span {
  background-image: url("assets/png/misc/bundle_orange.png");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2.5rem;
  padding-left: 4rem;
}

.map {
  height: 90vh;
  width: 100%;
  border: #923e08 solid 10px;
  border-radius: 8px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
}

.map-ctrl {
  margin: 15px;
}

.recenter {
  cursor: pointer;
  width: 100px;
  filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.5));
}

.clock {
  background-color: #ffd787;
  border: #f7ba00 solid 10px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.5);
  font-family: "VT323", Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
}

.i-season {
  border: #dc7b05 solid 3px;
  width: 42px;
}

footer {
  background-color: #09543b;
  padding: 3rem 0;
}
footer > * {
  margin: 1.5rem 0;
}

.i-heart {
  width: 3rem;
  vertical-align: middle;
}

.stack-i {
  width: 3rem;
  vertical-align: middle;
  margin: 0.2rem;
}
.stack-link {
  width: inherit;
}

.i-sass {
  width: 3.3rem;
}
.i-js {
  width: 2.61rem;
}
.i-gmaps {
  width: 2.55rem;
}